import * as d3 from 'd3';

export default class LineDisplay {
    constructor(lnHolder, lnWidth, lnHeight, lnData) {
        this.h = lnHeight;
        this.w = lnWidth;
        this.holder = lnHolder;
        this.dataset = lnData;
        this.lineFun = d3.line()
            .x((d, i) => i * ((this.w - 30) / this.dataset.length) + 40)
            .y(d => this.h - d.temp * 50 - 190)
            .curve(d3.curveLinear);

        this.buildLineChart();
        // this.fetchData();
    }

    buildLineChart() {
        let svg = d3.select(this.holder)
            .attr("width", this.w+80)
            .attr("height", this.h+40);
            
        let viz = svg.append("path")
            .attr("d", this.lineFun(this.dataset))
            .attr("stroke", "purple")
            .attr("stroke-width", 2)
            .attr("fill", "none");

        svg.append("g")
            .attr("class","textGroup")
            .selectAll("text")
            .data(this.dataset)
            .enter()
            .append("text")
            .attr("display","none")
            .text(d => d.temp)
            .attr("x", (d, i) => i * ((this.w - 30) / this.dataset.length) + 31)
            .attr("y", d => {
                if (d.temp < 0) {
                    return (this.h - d.temp * 50 - 180);
                } else {
                    return (this.h - d.temp * 50 - 200);
                }
            })

            .attr("font-size", "15px")
            .attr("font-family", "sans-serif")
            .attr("fill", "#883509")
            .attr("text-anchor", "start")
            .attr("dy", "0.35em")
            .attr("font-weight", (d, i) => {
                if (i === 0 || i === (this.dataset.length - 1)) {
                    return "bold";
                } else {
                    return "normal";
                }
            })

        // append a circle for each datapoint
        svg.append('g')
            // .attr('transform', 'translate(30, 0)')
            .selectAll("circle")
                .data(this.dataset)
                .enter()
                .append("circle")
                    .attr("cx",(d,i) => i * ((this.w - 30) / this.dataset.length) + 40)
                    .attr("cy",d => this.h - d.temp * 50 - 190)
                    .attr("r",3)
                    .attr("fill","#666")


        let scale = d3.scaleLinear()
            .domain([5.1, -3])
            .range([-55, 360]);

        let y_axis = d3.axisRight()
            .scale(scale);

        svg.append("g")
            .attr("transform", "translate(1180, 105)")
            .call(y_axis)
            .attr("font-size","15px");


        let scale2 = d3.scaleLinear()
            .domain([1970, 2012])
            .range([0, 1144.5]);

        let x_axis = d3.axisBottom()
            .scale(scale2).tickFormat(d3.format("d"));

        svg.append("g")
            .attr("transform", "translate(40, 435)")
            .call(x_axis)
                .attr("font-size","18px");

        //y-axis label
        svg.append("g")
            .append("text").text("Temperature").attr("x", this.w-40).attr("y", this.h - 460)
            .attr("class","axisLables")
            .attr("font-size", "20px").attr("fill", "#003366")

       //x-axis label
        svg.append("g")
            .append("text").text("Year").attr("y", 485).attr("x", 1/2*this.w)
            .attr("class","axisLables")
            .attr("font-size", "20px").attr("fill", "#003366")
        
        // adding legend 
        let curveLegend = svg.append("rect")
            .attr("x","950").attr("y","42").attr("width","60").attr("height","2").attr("stroke","none").attr("fill","purple")
        
        
        svg.append("g")
            .append("text").text("Temperature").attr("y", 50).attr("x", 840)
            .attr("class","axisLables")
            .attr("font-size", "18px").attr("fill", "#3C3F42");

        svg.append("g")
            .append("text").text("( Hover on bars or line to see numbers! )").attr("y", 100).attr("x", 380)
            .attr("class","axisLables")
            .attr("font-size", "24px").attr("fill", "grey");

    }


}