import * as d3 from "d3";


export default class BarDisplay {
    constructor(barHolder, barWidth, barHeight, barPadding, barData) {
        this.w = barWidth;
        this.h = barHeight;
        this.padding = barPadding;
        this.barHolder = barHolder;
        this.dataset=barData;
        this.buildBarChart();
        
    }

    buildBarChart() {
        // setting the size of the svg canvas
        let svg = d3.select(this.barHolder)
            .attr("width", this.w+40)
            .attr("height", this.h);

        // drawing the bars
        svg.selectAll("rect")
            .data(this.dataset)
            .enter()
            .append("rect")
            .attr("x", (d, i) => i * ((this.w-30) / this.dataset.length)+40)
            .attr("y", d => {
                if (d.precip < 0) {
                    return this.h - 200;
                } else {
                    return (this.h - (d.precip * 4 + 200));
                }
            })
            .attr("width", this.w / this.dataset.length - this.padding)
            .attr("height", d => {
                if (d.precip < 0) {
                    return d.precip * (-1) * 4;
                } else {
                    return d.precip * 4;
                }
            })
            .attr("fill", d => {
                if (d.precip < 0) {
                    return `rgb(${d.precip*(-1)*5+50},155,50)`;
                } else {
                    return `rgb(50,${d.precip*3+70},${d.precip*5+90})`;
                }
            });
        
        // attach lables to each bar
        svg.selectAll("text")
            .data(this.dataset)
            .enter()
            .append("text")
            .attr("class","textLables")
            .attr("display","none")
            .text(d => d.precip)
            .attr("x",(d, i) => i * ((this.w-30)/ this.dataset.length)+45)
            .attr("y",d => {
                if (d.precip < 0) {
                    return this.h - 200 + d.precip*(-1)*4+10;
                } else {
                    return (this.h - (d.precip * 4 + 210));
                }
            })
            .attr("font-size", "14px")
            .attr("font-family", "sans-serif")
            .attr("fill", "#666")
            .attr("text-anchor", "start")
            .attr("dy", "0.35em")

        // set the Y_scale 
        let y_scale = d3.scaleLinear()
                    .domain([61, -39])
                    .range([0, 400]);

        // define the y_axis, apply the scale 
        let y_axis = d3.axisLeft()
                    .scale(y_scale);


        // set the X_scale 
        let x_scale = d3.scaleLinear()
            .domain([1970, 2012])
            .range([0, 1144.5]);

        // define the x_axis, apply the X_scale 
        let x_axis = d3.axisBottom()
            .scale(x_scale).tickFormat(d3.format("d"));
        
        // create a group, within which draw the y axis
        svg.append("g")
            .attr("transform", "translate(39, 56)")
            .call(y_axis)
            .attr("font-size","15px");

        // create a group, within which draw the x axis
        svg.append("g")
            .attr("transform", "translate(40, 435)")
            .call(x_axis)
            .attr("font-size","18px");

        //add y-axis legend
        svg.append("g")
            .append("text").text("Precipitation").attr("x",5).attr("y",this.h - 460)
            .attr("class","axisLables")
            .attr("font-size", "20px").attr("fill", "#003366")

        //add x-axis legend
        svg.append("g")
            .append("text").text("Year").attr("y", 485).attr("x", 1/2*this.w)
            .attr("class","axisLables")
            .attr("font-size", "20px").attr("fill", "#003366")

        // add the color range legend
        let defs = svg.append("defs")

        let gradient1=defs.append("linearGradient").attr("id","lg01").attr("x1","0%").attr("y1","0%").attr("x2","100%").attr("y2","0%")
        
        let gradient2=defs.append("linearGradient").attr("id","lg02").attr("x1","0%").attr("y1","0%").attr("x2","100%").attr("y2","0%")

        gradient1.append("stop").attr("offset","0%").attr("style","stop-color:#0454A4")

        gradient1.append("stop").attr("offset","50%").attr("style","stop-color:#4293E4")
        
        gradient1.append("stop").attr("offset","100%").attr("style","stop-color:#42D5E4")


        gradient2.append("stop").attr("offset","0%").attr("style","stop-color:#3B8809")

        gradient2.append("stop").attr("offset","50%").attr("style","stop-color:#A4AB0D")
        
        gradient2.append("stop").attr("offset","100%").attr("style","stop-color:#E4BA42")

        let colorLegend1 = svg.append("rect")
            .attr("x","450").attr("y","40").attr("width","60").attr("height","10").attr("stroke","none").attr("fill","url(#lg01)")
        
        let colorLegend2 = svg.append("rect")
        .attr("x","730").attr("y","40").attr("width","60").attr("height","10").attr("stroke","none").attr("fill","url(#lg02)")
        
        //add colorrange legend
        svg.append("g")
            .append("text").text("Positive Percentage").attr("y", 50).attr("x", 280)
            .attr("class","axisLables")
            .attr("font-size", "18px").attr("fill", "#3C3F42")
        
        svg.append("g")
            .append("text").text("Negative Percentage").attr("y", 50).attr("x", 550)
            .attr("class","axisLables")
            .attr("font-size", "18px").attr("fill", "#3C3F42");


        svg.append("g")
            .append("text").text("( Hover on bars or line to see numbers! )").attr("y", 100).attr("x", 380)
            .attr("class","axisLables")
            .attr("font-size", "24px").attr("fill", "grey");

    }
    


}