import BarDisplay from './BarDisplay';
import LineDisplay from './LineDisplay';
import { log } from 'util';


// assign a set value to the bar width, height, padding, and holder 
let barHolder = "#barSpace";
let barWidth = 1150;
let barHeight = 500;
let barPadding = 3;



// assign a set value to the line width, height, padding, and holder 
let lineHolder = "#lineSpace";
let lineWidth = 1150;
let lineHeight = 500;



// assign a set value to the mixed chart width, height, padding, and holder 
let mixHolder = "#mixSpace";
let mixWidth = 1150;
let mixHeight = 500;
let mixPadding = 3;

// declare the DOM varialbe 
const glakes = document.getElementsByClassName('glakes');
const arctic = document.getElementsByClassName('arctic');
const barSpace = document.getElementById('barSpace');
const lineSpace = document.getElementById('lineSpace');
const mixSpace = document.getElementById('mixSpace');
const region = document.getElementById('region');

// fetch data from external sources 
let dataset = [];
fetch('data.json')
        // get js to convert the json 
            .then(data => data.json())
            .then(data => {
                
                region.onchange = e => {
                    
                    barSpace.innerHTML="";
                    lineSpace.innerHTML="";
                    mixSpace.innerHTML="";
                    // decide which set of data to use based on what the user chooses
                    if(region.value=="greatLakes"){

                        dataset = data.rawData1;
                        for(let i=0;i<3;i++){
                            glakes[i].style.display = "block";
                            arctic[i].style.display = "none";
                        }

                    }else if(region.value=="arcticMountains"){

                        dataset = data.rawData2;
                        for(let i=0;i<3;i++){
                            arctic[i].style.display = "block";
                            glakes[i].style.display = "none";
                        }
                        
                    };
                    // make a new instance of BarDisplay
                    let myBars = new BarDisplay(barHolder, barWidth, barHeight, barPadding, dataset);
                    // make a new instance of LineDisplay
                    let myLines = new LineDisplay(lineHolder, lineWidth, lineHeight, dataset);
                    // make a new instance of MixDisplay
                    let myMixedBars = new BarDisplay(mixHolder, mixWidth, mixHeight, mixPadding, dataset);
                    let myMixedLines = new LineDisplay(mixHolder, mixWidth, mixHeight, dataset);
                    
                };

            });














